.AppCard {
  display: -ms-flexbox;
  display: flex;
  margin: 20px;
  position: relative;
  overflow: initial;
  -ms-flex-preferred-size: 0px;
      flex-basis: 0px;
  -ms-flex-positive: 1;
      flex-grow: 1;
  min-width: 350px;
}

.AppCard .image {
  width: 150px;
  height: 150px;
  background-color: #fd386a;
}

.AppCard a {
  text-decoration: none;
}

.AppCard .edit {
  position: absolute;
  bottom: 0;
  right: 0;
}



